.main-data {
  background-color: #eff1f3;
  height: 100vh;
}

.country-name {
  text-align: center;
  line-height: 1.5em;
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
  color: #424874;
}

/* react css transitions group */
.country-appear {
  opacity: 0.01;
}

/* react css transitions group */
.country-appear.country-appear-active {
  opacity: 1;
  -webkit-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
}

/* react css transitions group */
.country-enter {
  opacity: 0.01;
}

/* react css transitions group */
.country-enter.country-enter-active {
  opacity: 1;
  -webkit-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
}


/* react css transitions group */
.left-appear {
  opacity: 0.01;
  -webkit-transform: translateX(-200px);
      -ms-transform: translateX(-200px);
          transform: translateX(-200px);
}


/* react css transitions group */
.left-appear.left-appear-active {
  opacity: 1;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
}

/* react css transitions group */
.left-enter {
  opacity: 0.01;
  -webkit-transform: translateX(-200px);
      -ms-transform: translateX(-200px);
          transform: translateX(-200px)
}

/* react css transitions group */
.left-enter.left-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
}


/* react css transitions group */
.right-appear {
  opacity: 0.01;
  -webkit-transform: translateX(200px);
      -ms-transform: translateX(200px);
          transform: translateX(200px);
}


/* react css transitions group */
.right-appear.right-appear-active {
  opacity: 1;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
}

/* react css transitions group */
.right-enter {
  opacity: 0.01;
  -webkit-transform: translateX(200px);
      -ms-transform: translateX(200px);
          transform: translateX(200px)
}

/* react css transitions group */
.right-enter.right-enter-active {
  opacity: 1;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: all 2s ease-in-out;
  -o-transition: all 2s ease-in-out;
  transition: all 2s ease-in-out;
}




.section-wrapper {
  position: relative;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 96%;
  /* justify-content: space-evenly; */
  background-color: #EFF1F3;
  margin: 0 auto;
  color: #274060;
  border-radius: 2px;
  margin-bottom: 5px;
  /* padding: 3px; */
}

.label {
  background-color: #f88e33;
  width: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0 22px;
  position: relative;
}

